<template>
  <v-toolbar app height="64px" v-if="username">

    <v-btn fab color="primary" to="/" :disabled="checkInIconDisabled">
      <v-icon large>supervisor_account</v-icon>
    </v-btn>
    <v-spacer></v-spacer>

    <v-btn fab color="primary" to="/key" :disabled="keyIconDisabled">
      <v-icon large>vpn_key</v-icon>
    </v-btn>
    <v-spacer></v-spacer>

    <v-menu :nudge-width="100">
      <v-btn slot="activator" fab color="primary">
        <v-icon x-large>language</v-icon>
      </v-btn>


      <v-list>
        <v-list-tile
          v-for="item in languages"
          :key="item"
          @click.native="switchLocale(item)"
        >
          <v-list-tile-title>{{ $t( "menu." + item) }}</v-list-tile-title>
          <flag :iso="item" />
        </v-list-tile>
      </v-list>
    </v-menu>
    <v-spacer></v-spacer>

    <v-btn fab color="primary" to="/about">
      <v-icon large>help</v-icon>
    </v-btn>
    <v-spacer></v-spacer>

    <v-btn fab color="primary" @click.native="logout">
      <v-icon large>exit_to_app</v-icon>
    </v-btn>

  </v-toolbar>
</template>

<script>
import Vue from 'vue'
import { mapGetters, mapState, mapActions } from 'vuex'
import FlagIcon from 'vue-flag-icon'
import { EventBus } from '@/eventBus'
import * as utils from '@/utils'

Vue.use(FlagIcon)

export default {
  name: 'Menu',
  data: () => ({
    languages: [
      'gb', 'hk', 'jp'
    ],
    checkInIconDisabled: false,
    keyIconDisabled: false,

  }),
  created () {
  },
  mounted () {
    this.keyIconDisabled = !this.allIdMatches
    this.checkInIconDisabled = this.allIdMatches
  },
  computed: mapState({
    username: state => state.session.username,
    ...mapState('checkIn', {
      members: 'members',
    }),
    ...mapGetters('checkIn', {
      allIdMatches: 'allIdMatches',
    })
  }),
  watch: {
    members: {
      handler: function (val, oldVal) {
        console.log('members', val)
        this.keyIconDisabled = !this.allIdMatches
        this.checkInIconDisabled = this.allIdMatches
      },
      deep: true
    },
  },
  methods: {
    switchLocale (item) {
      this.$i18n.locale = item
    },
    toggleFullScreen() {
      try {
        utils.ToggleFullScreen()  
      } 
      catch (e) {
        window.alert(e)
      }
      
    },
    ...mapActions({
      logout: 'session/logout',
    })
  },
}
</script>